      <div class="news-article" aria-label="Noticia que incluye un tráiler de película.">
        <h2 class="news-title">Tráiler de la Película: "El Silencio"</h2>
        <p class="news-body" aria-label="Descripción del tráiler.">
          Disfruta del emocionante tráiler de esta nueva película de acción con la nueva tecnología de sonido.
        </p>
        <div class="youtube-trailer" aria-label="Video de YouTube mostrando un tráiler.">
