            title="Tráiler de la película UP, mostrando el día a día de un hombre mayor en su rutina matutina"
            aria-label="Un tráiler que muestra el día a día de un hombre mayor en su rutina matutina."
            frameborder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen
          ></iframe>
        </div>
      </div>

      <!-- Hits del Momento -->
      <div class="news-article" aria-label="Noticia sobre los hits musicales más populares del momento.">
        <h2 class="news-title">Hits del Momento</h2>
        <p class="news-body" aria-label="Descripción de los hits musicales.">
          Sumérgete en el ritmo de las tendencias actuales y descubre los éxitos que están marcando el pulso de la música en todo el mundo. Desde los ritmos pegajosos del reguetón hasta las baladas más emotivas, estas canciones han conquistado las listas y se han convertido en la banda sonora de millones de personas. Escucha las canciones más populares del momento y déjate llevar por las melodías que están definiendo esta temporada.
        </p>
        <div
          class="audio-player"
