</template>

<script>
export default {
  name: "PagCultura",
  data() {
    return {
      hits: [
        { title: "Moscow Mule 'Bad Bunny'" },
        { title: "Khé 'Rauw Alejandro'" },
        { title: "Propuesta Indecente 'Romeo Santos'" },
      ],
      activeTrack: null, // Pista que se está "reproduciendo"
    };
  },
  methods: {
    playFakeAudio(index) {
      this.activeTrack = index;
      setTimeout(() => {
        this.activeTrack = null; // Simula que la pista termina
      }, 3000); // Duración de la "reproducción"
    },
  },
};
