<template>
  <section class="section-container">
    <!-- Encabezado -->
    <header class="section-header" aria-label="Encabezado de la sección de deportes">
      <h1 class="headline">Deportes</h1>
      <p class="subheadline" aria-label="Subtítulo de la sección: Noticias destacadas del FC Barcelona y más">
        "Noticias destacadas del FC Barcelona y más"
      </p>
    </header>

    <!-- Noticias Deportivas -->
    <article class="news-content">
      <!-- Noticia 1: Partido del Barça -->
      <div class="news-article" aria-label="Artículo sobre el partido del FC Barcelona">
        <h2 class="news-title">El Barça triunfa en un emocionante clásico</h2>
        <p class="news-body" aria-label="Descripción del partido entre el FC Barcelona y el Real Madrid">
          En un encuentro vibrante, el FC Barcelona logró una victoria crucial frente al Real Madrid con un marcador de 3-2.
          Lewandowski brilló con dos goles, mientras que Gavi cerró el marcador con un disparo espectacular desde fuera del área.
          Este resultado coloca al Barça como líder provisional de La Liga.
        </p>
        <p class="news-action" aria-label="Botón para comprar entradas para el partido">
          <button
            class="confusing-button"
            tabindex="0"
            aria-label="Comprar entradas para el partido del FC Barcelona"
          >
            Comprar entradas
          </button>
        </p>
      </div>

      <!-- Noticia 2: Video destacado -->
      <div class="news-article" aria-label="Artículo con video destacado sobre el gol de la victoria">
        <h2 class="news-title">Revive el gol de la victoria</h2>
        <p class="news-body" aria-label="Descripción del gol de Gavi en el minuto 89">
          El gol de Gavi en el minuto 89 dejó a la afición al borde de la locura. Revive el momento aquí:
        </p>
        <!-- Tráiler adaptado -->
        <div class="youtube-trailer" aria-label="Video de YouTube mostrando el gol de la victoria">
          <iframe
            width="560"
            height="315"
            src="https://www.youtube.com/embed/AlOhcJ6twsc?autoplay=1&mute=1"
            title="Video destacado del gol de Gavi"
            aria-label="Video mostrando el gol de la victoria de Gavi contra el Real Madrid"
            frameborder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen
          ></iframe>
        </div>
       <!-- Botón Pausa -->
       <div class="pause-container">
          <button class="moving-button" @mouseover="moveButton" @touchstart="moveButton">Pausa</button>
        </div>
      </div>

      <!-- Noticia 3: Tabla de clasificación -->
      <div class="news-article" aria-label="Tabla de clasificación de La Liga">
        <h2 class="news-title">Clasificación actualizada de La Liga</h2>
        <p class="news-body" aria-label="Descripción de la clasificación actual de La Liga">
          Consulta la tabla de clasificación después de esta jornada emocionante:
        </p>
        <table class="accessible-table" tabindex="0" aria-label="Tabla mostrando las posiciones de los equipos en La Liga">
          <thead>
            <tr>
              <th scope="col" aria-label="Posición en la clasificación">Posición</th>
              <th scope="col" aria-label="Nombre del equipo">Equipo</th>
              <th scope="col" aria-label="Puntos obtenidos">Puntos</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="team in teams" :key="team.position">
              <td>{{ team.position }}</td>
              <td>{{ team.team }}</td>
              <td>{{ team.points }}</td>
            </tr>
          </tbody>
        </table>
        <p class="news-action" aria-label="Botón para ver más equipos en la clasificación">
          <button
            class="keyboard-only-button"
            tabindex="0"
            aria-label="Ver más equipos en la clasificación de La Liga"
          >
            Ver más
          </button>
        </p>
      </div>
    </article>
  </section>
</template>

<script>
export default {
  name: "PagDeportes",
  data() {
    return {
      teams: [], // Datos para la tabla de clasificación
    };
  },
  mounted() {
    this.fetchTeams();
  },
  methods: {
    async fetchTeams() {
      try {
        const response = await fetch("./classification.json"); // Archivo JSON con los equipos
        const data = await response.json();
        this.teams = data;
      } catch (error) {
        console.error("Error al cargar el JSON:", error);
      }
    },
    moveButton(event) {
      const button = event.target;
      const parent = button.parentElement;
      const maxX = parent.clientWidth - button.offsetWidth;
      const maxY = parent.clientHeight - button.offsetHeight;

      const randomX = Math.random() * maxX;
      const randomY = Math.random() * maxY;

      button.style.transform = `translate(${randomX}px, ${randomY}px)`;
    },
  },
};
</script>

<style scoped>
/* Contenedor general */
.section-container {
  font-family: "Georgia", serif;
  padding: 2rem;
  background-color: #fdfdfd;
  border: 1px solid #ccc;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

/* Encabezado */
.section-header {
  text-align: center;
  margin-bottom: 2rem;
}

.headline {
  font-size: 2.5rem;
  font-weight: bold;
}

.subheadline {
  font-size: 1.2rem;
  font-style: italic;
}

/* Noticias */
.news-content {
  line-height: 1.8;
}

.news-article {
  margin-bottom: 2rem;
  padding: 1rem;
  border: 1px solid #ccc;
  background-color: #f7f7f7;
  border-radius: 8px;
}

.news-title {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.news-body {
  margin-bottom: 1rem;
  text-align: justify;
}

.news-action {
  text-align: center;
}

/* Tráiler adaptado */
.youtube-trailer {
  width: 100%;
  max-width: 600px;
  margin: auto;
  overflow: hidden;
  border-radius: 5px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}

.youtube-trailer iframe {
  width: 100%;
  aspect-ratio: 16 / 9; /* Mantiene la proporción estándar de un video */
  border: none;
}

/* Botón Pausa */
.pause-container {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  position: relative;
  height: 50px; /* Contenedor para el movimiento del botón */
}

.moving-button {
  font-size: 1.2rem;
  padding: 0.5rem 1.5rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  position: absolute;
  transition: transform 0.2s ease;
}

.moving-button:hover {
  background-color: #0056b3;
}

/* Botones confusos */
.confusing-button,
.keyboard-only-button {
  font-size: 1.2rem;
  padding: 0.5rem 1.5rem;
  background-color: transparent;
  border: none;
  color: #333;
  position: relative;
  transition: color 0.3s ease, transform 0.2s ease;
}

.confusing-button:hover,
.keyboard-only-button:hover {
  color: transparent; /* Hace que el hover sea confuso para el ratón */
  cursor: none; /* Elimina la interacción del ratón */
}

.confusing-button:focus,
.keyboard-only-button:focus {
  color: #007bff;
  transform: scale(1.05);
  outline: 2px solid #007bff;
}

iframe {
  width: 100%;
  height: 100%;
  pointer-events: none; /* Bloquea la interacción del ratón */
}

iframe:focus {
  pointer-events: auto; /* Permite la interacción con el teclado */
  outline: 2px solid #007bff; /* Resalta cuando está enfocado */
}

/* Tabla accesible */
.accessible-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
}

.accessible-table th,
.accessible-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.accessible-table th {
  background-color: #007bff;
  color: white;
}

.accessible-table tbody tr:nth-child(even) {
  background-color: #f2f2f2;
}

.accessible-table:focus {
  outline: 2px solid #007bff;
}

@media screen and (max-width: 768px) {
  /* General para contenedores */
  .section-container {
    padding: 1rem;
    font-size: 0.9rem;
  }

  /* Encabezados */
  .headline {
    font-size: 2rem;
  }

  .subheadline {
    font-size: 1rem;
  }

  /* Navegación */
  nav ul {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  nav ul li {
    margin: 0;
  }

  /* Botones */
  .keyboard-only-button, .small-button {
    width: 100%;
    font-size: 1rem;
  }

  /* Noticias */
  .news-content {
    display: block;
  }

  .news-article {
    margin-bottom: 1rem;
    padding: 0.5rem;
  }

  /* Video Responsivo */
  .youtube-trailer iframe {
    width: 100%;
    height: auto;
  }

  /* Tablas Responsivas */
  .accessible-table {
    font-size: 0.9rem;
    overflow-x: auto;
    display: block;
  }

  /* Footer */
  .footer-nav {
    flex-direction: column;
    gap: 0.5rem;
  }
}

@media screen and (max-width: 480px) {
  /* Títulos */
  .headline {
    font-size: 1.8rem;
  }

  .subheadline {
    font-size: 0.9rem;
  }

  /* Navegación */
  nav ul {
    font-size: 0.9rem;
  }

  .footer-nav {
    font-size: 0.8rem;
  }
}

@media screen and (min-width: 1024px) {
  /* Ajustes para pantallas grandes */
  .section-container {
    max-width: 1200px;
    margin: auto;
  }

  .section-navigation {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
</style>
