<template>
  <section
    class="section-container"
    @mousemove="updateMousePosition"
    @touchmove="updateTouchPosition"
    :style="{ '--mouse-x': mouseX + 'px', '--mouse-y': mouseY + 'px' }"
  >
    <!-- Encabezado -->
    <header class="section-header">
      <h1 class="headline">Sucesos</h1>
      <p class="subheadline">"Noticias destacadas en tu área"</p>
    </header>

    <!-- Contenido -->
    <article class="news-content">
      <div class="news-article" @mouseover="activateDistortion" @mouseleave="deactivateDistortion">
        <h2 class="news-title distorted-text">Robo en el centro de la ciudad</h2>
        <p class="news-body distorted-text">
          Un robo espectacular tuvo lugar anoche en el centro de la ciudad. Los ladrones escaparon con una suma millonaria,
          dejando a las autoridades sin pistas claras sobre su paradero.
        </p>
      </div>
      <div class="news-article" @mouseover="activateDistortion" @mouseleave="deactivateDistortion">
        <h2 class="news-title distorted-text">Accidente en la autopista</h2>
        <p class="news-body distorted-text">
          Esta mañana, un grave accidente en la autopista dejó a varios heridos. Los servicios de emergencia atendieron la
          situación rápidamente, pero el tráfico se vio severamente afectado.
        </p>
      </div>
      <div class="news-article" @mouseover="activateDistortion" @mouseleave="deactivateDistortion">
        <h2 class="news-title distorted-text">Descubrimiento arqueológico</h2>
        <p class="news-body distorted-text">
          Arqueólogos locales han hecho un descubrimiento fascinante en las afueras de la ciudad, desenterrando artefactos
          que datan de hace más de 2,000 años.
        </p>
      </div>
    </article>
  </section>
</template>

<script>
export default {
  name: "PagSucesos",
  data() {
    return {
      mouseX: 0,
      mouseY: 0,
    };
  },
  methods: {
    updateMousePosition(event) {
      this.mouseX = event.clientX;
      this.mouseY = event.clientY;
    },
    updateTouchPosition(event) {
      if (event.touches && event.touches.length > 0) {
        this.mouseX = event.touches[0].clientX;
        this.mouseY = event.touches[0].clientY;
      }
    },
    activateDistortion(event) {
      const target = event.currentTarget;
      target.classList.add("distorted");
    },
    deactivateDistortion(event) {
      const target = event.currentTarget;
      target.classList.remove("distorted");
    },
  },
};
</script>

<style scoped>
/* Contenedor general */
.section-container {
  position: relative;
  font-family: "Georgia", serif;
  padding: 2rem;
  background-color: #fdfdfd;
  border: 1px solid #ccc;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  height: 100vh;
  width: 100%;
  --mouse-x: 50%; /* Coordenada X inicial */
  --mouse-y: 50%; /* Coordenada Y inicial */
}

/* Cubrir toda la pantalla con negro excepto el área visible */
.section-container::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle 100px at var(--mouse-x) var(--mouse-y), transparent 0%, black 100%);
  pointer-events: none;
  z-index: 999;
}

/* Contenido */
.news-content {
  position: relative;
  z-index: 1;
}

.news-article {
  margin-bottom: 2rem;
  transition: filter 0.3s, transform 0.3s;
}

.news-title {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.news-body {
  margin-bottom: 1rem;
  text-align: justify;
}

/* Efecto de distorsión */
.distorted {
  filter: blur(3px);
  transform: scale(1.05) rotate(0.5deg);
}

.distorted-text {
  transition: filter 0.3s, transform 0.3s;
}

@media screen and (max-width: 768px) {
  /* Ajustes para móviles */
  .section-container {
    padding: 1rem;
    font-size: 0.9rem;
  }

  .headline {
    font-size: 2rem;
  }

  .subheadline {
    font-size: 1rem;
  }

  .news-article {
    margin-bottom: 1rem;
    padding: 0.5rem;
  }
}
</style>
