import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue';
import Sucesos from '../views/PagSucesos.vue';
import CulturaOcio from '../views/PagCultura.vue';
import Economia from '../views/PagEconomia.vue';
import Deportes from '../views/PagDeportes.vue';
import AboutView from '../views/AboutView.vue';
import ContactView from '../views/ContactView.vue';

Vue.use(VueRouter)

const routes = [
  { path: '/', name: 'Home', component: HomeView },
  { path: '/sucesos', name: 'Sucesos', component: Sucesos },
  { path: '/cultura-ocio', name: 'CulturaOcio', component: CulturaOcio },
  { path: '/economia', name: 'Economia', component: Economia },
  { path: '/deportes', name: 'Deportes', component: Deportes },
  { path: '/about', name: 'About', component: AboutView },
  { path: '/contact', name: 'Contact', component: ContactView },
  { path: '/:catchAll(.*)', name: 'NotFound', component: () => import('../views/NotFound.vue') },
];

const router = new VueRouter({
  mode: "history",
  base: '/perspectivadigital/',
  routes
})

export default router;
