<template>
    <div class="contact container mt-5 newspaper-style">
      <div class="text-center">
        <h1 class="headline">Contáctanos</h1>
        <p class="lead mt-4">
          En Perspectiva Digital, valoramos tus comentarios y sugerencias. Completa el formulario a continuación y nos pondremos en contacto contigo lo antes posible.
        </p>
      </div>
  
      <form @submit.prevent="submitForm" class="mt-5">
        <div class="mb-3">
          <label for="name" class="form-label">Nombre</label>
          <input
            type="text"
            id="name"
            class="form-control"
            v-model="name"
            placeholder="Ingresa tu nombre"
            required
          />
        </div>
        <div class="mb-3">
          <label for="email" class="form-label">Correo Electrónico</label>
          <input
            type="email"
            id="email"
            class="form-control"
            v-model="email"
            placeholder="Ingresa tu correo"
            required
          />
        </div>
        <div class="mb-3">
          <label for="message" class="form-label">Mensaje</label>
          <textarea
            id="message"
            class="form-control"
            v-model="message"
            placeholder="Escribe tu mensaje aquí"
            rows="5"
            required
          ></textarea>
        </div>
        <button type="submit" class="btn btn-outline-dark btn-lg">Enviar</button>
      </form>
    </div>
  </template>
  
  <script>
  export default {
    name: "ContactView",
    data() {
      return {
        name: "",
        email: "",
        message: "",
      };
    },
    methods: {
      submitForm() {
        alert(`¡Gracias, ${this.name}! Hemos recibido tu mensaje.`);
        this.name = "";
        this.email = "";
        this.message = "";
      },
    },
  };
  </script>
  
  <style scoped>
  /* Estilo General */
  .newspaper-style {
    background-color: #fdfdfd;
    border: 1px solid #ccc;
    padding: 2rem;
    font-family: 'Georgia', serif;
    color: #333;
    line-height: 1.6;
  }
  
  /* Titulares y Encabezados */
  .headline {
    font-family: 'Times New Roman', Times, serif;
    font-weight: bold;
    font-size: 2.5rem;
    border-bottom: 2px solid #000;
    padding-bottom: 0.5rem;
  }
  
  .lead {
    font-size: 1.2rem;
    margin-bottom: 1rem;
  }
  
  /* Formulario */
  .form-control {
    font-size: 1rem;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .form-control:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0px 0px 5px rgba(0, 123, 255, 0.5);
  }
  
  .btn-outline-dark {
    font-size: 1.2rem;
    border: 2px solid #333;
    padding: 0.5rem 1.5rem;
    text-transform: uppercase;
  }
  
  .btn-outline-dark:hover {
    background-color: #333;
    color: #fff;
  }
  </style>